.SelectBox {
  fill: rgba(0, 0, 0, 0);
}

.Sheet .SelectBox {
  pointer-events: none;
}

.SelectBox--selected {
  fill: rgba(0, 100, 255, 0.2);
}

.MainBox {
  fill: rgba(0, 0, 0, 0);
  pointer-events: none;
}
.Sheet .MainBox {
  pointer-events: all !important;
}

.Repeat {
  text-anchor: middle;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.Setter {
  text-anchor: middle;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.CategoryMenu {
  padding-top: 5px;
  padding-bottom: 5px;
  background: rgb(87, 105, 127);
  cursor: default;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.CategoryMenu button {
  cursor: pointer;
  border: none;
  text-align: center;
  display: block;
  margin: auto;
  background: rgb(0, 0, 0, 0);
  color: #fff;
}

.CategoryMenu button.Selected {
  color: rgb(152, 220, 255);
}

.CategoryMenu button:focus {
  outline: 0;
}

.Palette {
  -webkit-flex: 0 0 100px;
          flex: 0 0 100px;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.Palette_scroll > svg {
  width: 50px;
  margin-left: 35px;
  -webkit-transform-origin: top left;
          transform-origin: top left;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}

.Palette_scroll {
  -webkit-flex: 1 1;
          flex: 1 1;
  overflow: auto;
  overflow-x: hidden;
}

.PaletteItem {
  cursor: pointer;
}

.PlayControls {
  position: fixed;
  top: 40px;
  right: 15px;
}

.PlayControls button {
  display: block;
  margin-bottom: 0.5em;
}

.Clef {
  fill: #000;
  -webkit-transform: scale(3, 3);
          transform: scale(3, 3);
}


.StaffLine {
  stroke: #000;
  stroke-width: 1px;
  pointer-events: none;
}

.ChordGuideline {
  stroke: #ddd;
  pointer-events: none;
}

.StaffIndex {
  fill: #bbb;
}

.Sheet {
  width: 100%;
  height: 100vh;
  overflow: auto;
  background: #fff;
  padding-top: 5px;
}

.Sheet > svg {
  z-index: 1;
}

.SheetTabs {
  width: 100%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.SheetTabs_TabZone {
  cursor: default;
  padding-top: 3px;
}

.SheetTabs_Tab {
  float: left;
  /* Replicate antd buttons */
  line-height: 1.499;
  background: #e4e4e4;
  border: 1px solid #d9d9d9;
  border-radius: 4px 4px 0 0;
  color: rgba(0, 0, 0, 0.65);
  height: 32px;
  padding: 0 15px;
  margin-right: 1px;
  outline: none;
  cursor: pointer;
}

.SheetTabs_Tab--active {
  background: #fff;
  border-bottom: none;
}

.App {
  display: -webkit-flex;
  display: flex;
  font-family: Avenir, Helvetica, Arial, sans-serif;
}

.App--dragging {
  cursor: -webkit-grabbing;
  cursor: grabbing;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.App--drag {
  cursor: -webkit-grab;
  cursor: grab;
}

.App--select {
  cursor: default;
}

.App--insert {
  cursor: default;
}

.ant-tabs {
  width: 100%;
  background: #fff;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  height: 100vh;
  overflow: auto;
}

body,
html {
  background: rgb(44, 57, 74);
  padding: none;
  margin: 0;
}

